// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue';
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';
import i18n from './i18n/i18n';
import App from './App';
import router from './router';
import axios from 'axios';
import * as types from './store/types';
import store from './store/store';
import { SERVER_API_URL } from '@/conf/constvar';

Vue.use(ElementUI);

import '@/assets/css/index.styl';
Vue.config.productionTip = false;

// 路由守卫
router.beforeEach((to, from, next)=>{
  store.state.contentLoading=false;
  if(store.state.pswdreminder)store.state.pswdreminder.close();//页面跳转，关闭修改密码提示
  
  if(to.meta.requireAuth){
    if(window.sessionStorage.getItem("mytoken")){
      next();
    }else {
      next({
        path: '/login',
        query: {redirect: to.fullPath}
      })
    }
  }else{
    next();
  }
});

//倒计时退出弹框
showConfirm(); //刷新页面时
export function showConfirm(){
  const activeTime = sessionStorage.getItem('expires_in')*1000;//每个token的有效时间
  if(!activeTime){ 
    return false;
  }
  store.state.LoginResTime=setTimeout(()=>{
    store.state.loginResShow=true;
  },activeTime)
}

//axios.defaults.headers.common['Authorization'] = "AUTH_TOKEN";
// http request 拦截器
axios.interceptors.request.use(
  config => {
    const mytoken = sessionStorage.getItem('mytoken');
    if(mytoken) {  // 判断是否存在token，如果存在的话，则每个http header都加上token
      config.headers.Authorization = `Bearer ${mytoken}`;
    }
    return config;
  },
  err => {
    return Promise.reject(err);
});

// http response 拦截器
var isRefreshingFlag = true;//这儿设置一个刷新token的开关isRefreshingFlag，这个是非常有必要的，防止重复请求。//参考https://blog.csdn.net/w13707470416/article/details/85222337
axios.interceptors.response.use(
  response => {//请求成功
    return response
  },
  err => {
    if(err.response) { //功能：定时替换token（不经过用户，token到期就自行替换token）（注意！！！loginRestriction组件询问用户是否继续访问页面时用户点击继续，也会替换token；现在虽然写了loginRestriction组件但是（轮询、刷新页面可能进入下面代码））
      switch (err.response.status) {
        case 401://token过期则后台返回401，此时中断请求(并记录该请求err.config)，然后马上请求新的token，用新token替换掉原来过期的旧token，再继续之前那个被中断的请求
        let config = err.config;
        if(isRefreshingFlag){
          refreshTokenRequst();
        }
        isRefreshingFlag = false;
        const retryOriginalRequest = new Promise((resolve) => {
          addSubscriber(()=> {
            resolve(axios(config))
          })
        });
        return retryOriginalRequest;
        break;
        default:
        apps.$message.error('请求失败，请刷新页面重试');
      }
    }else{
      apps.$message.error('服务器未响应！');
    }
    return Promise.reject(err)// return接口返回的错误信息
  }
);

//请求新token 
function refreshTokenRequst(){
  const oldRefreshToken = sessionStorage.getItem('refresh_token');
  var url=SERVER_API_URL+"uaa/oauth/token?refresh_token="+oldRefreshToken+"&grant_type=refresh_token";
  const instance = axios.create();//实例化，独立于拦截器
  instance.post(url,{},{
    headers: {
      'Authorization': 'Basic VUk6V0VScU0zWnVORFIxZEhWQw==',
    }
  }).then((response)=>{
    isRefreshingFlag = true;
    store.commit(types.LOGIN,response.data);
    onAccessTokenFetched();
  }).catch(err=>{
    isRefreshingFlag = true;
    alert("登录超时,请重新登录！");
    store.commit(types.LOGOUT);
  })
}

//拿到新token后再继续之前的请求
let subscribers = [];
function onAccessTokenFetched() {
  subscribers.forEach((callback)=>{
    callback();
  })
  subscribers = [];
}

//保存之前token过期的请求，（可能有多个或一个请求）。
function addSubscriber(callback) {
  subscribers.push(callback)
}

var apps = new Vue({
  el: '#app',
  router,
  store,
  i18n,
  template: '<App/>',
  components: { App }
});






